function Home() {
    return (
        <div>
            <section id="about" className="block about-block">
                <Container fluid>
                    <div className="title-holder">
                        <h2>TNG QUAN TRNG CAO NG AN NINH MNG ISPACE</h2>
                        <div className="subtitle">iSPACE l n v thuc NGS Group</div>
                    </div>
                    <Row>
                        <Col sm={6}>
                            <Image src={img1} />
                        </Col>
                        <Col sm={6}>
                            <p>
                                Trng Cao ng An ninh mng iSPACE l n v thuc NGS Group. iSPACE tin phong trong
                                o to thc hnh t nm 2008 theo tiu ch tuyn sinh i lin tuyn dng.
                            </p>

                            <p>
                                T nm 2015, Trng iSPACE thc hin chng trnh tuyn sinh v o to c bit CNTT-
                                Hc  lm vic ngay. Chng trnh mang n cho cc bn tr am m CNTT ba gi tr vt
                                tri: 70% hc thc hnh, 100% lm vic ngay, 100% thm ngh ph.
                            </p>

                            <p>
                                Nm hc 2021- 2022, Trng Cao ng An ninh mng iSPACE m rng v nng cp c s vt
                                cht vi quy m hin i v sng to theo xu hng trng hc quc t.
                            </p>
                        </Col>
                    </Row>
                </Container>
            </section>
            <Program />

            <section id="about" className="block about-block">
                <Container fluid>
                    <div className="title-holder">
                        <h2>18 NM HNH THNH V PHT TRIN</h2>
                    </div>
                    <Row>
                        <Col sm>
                            <div className="d-flex justify-content-center">
                                <Image src={img2} />
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
            <section>
                <CardGroup>
                    {studentData.map((student) => {
                        return (
                            <Card style={{ border: 'none', width: '150px', height: '250px' }}>
                                <Card.Img variant="top" src={student.image} />
                                <Card.Body>
                                    <Card.Title>{student.title}</Card.Title>
                                    <Card.Text>{student.subtitle}</Card.Text>
                                </Card.Body>
                            </Card>
                        );
                    })}
                </CardGroup>
            </section>
            <Number />
            <Teacher />
            <Carousel />
            {/* business */}
            <section id="business" className="block business-block">
                <Container fluid>
                    <div className="title-holder">
                        <h2>Doanh nghip</h2>
                    </div>
                    <Row className="portfolioList">
                        {businessData.map((bus) => {
                            return (
                                <Col sm={4} key={bus.id}>
                                    <div className="portfolioList-wrapper">
                                        <a href="#">
                                            <Image src={bus.image} />
                                        </a>
                                    </div>
                                </Col>
                            );
                        })}
                    </Row>
                </Container>
            </section>
            <section id="blog" className="block blog-block">
                <Container fluid>
                    <div className="title-holder">
                        <h2>TIN NI BT</h2>
                        <div className="subtitle">Learning by Doing</div>
                    </div>
                    <Row>
                        {blogData.map((blog) => {
                            return (
                                <Col sm={3} key={blog.id}>
                                    <div className="holder">
                                        <Card>
                                            <Card.Img variant="top" src={blog.image} />
                                            <Card.Body>
                                                <time>{blog.time}</time>
                                                <Card.Title>{blog.title}</Card.Title>
                                                <Card.Text>{blog.description}</Card.Text>
                                                <a href="#" className="btn btn-primary">
                                                    Read More
                                                </a>
                                            </Card.Body>
                                        </Card>
                                    </div>
                                </Col>
                            );
                        })}
                    </Row>
                </Container>
            </section>
        </div>
    );
}

export default Home;
