import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';

const programData = [
    { id: 1, image: require('../assets/images/ANM_3.png'), title: 'AN NINH MNG', lable: 'NG K' },
    { id: 2, image: require('../assets/images/PTPM_3.png'), title: 'PHT TRIN PHN MM', lable: 'NG K' },
    { id: 3, image: require('../assets/images/TKDH_3.png'), title: 'THIT K  HA', lable: 'NG K' },
    { id: 4, image: require('../assets/images/TMDT.png'), title: 'THNG MI IN T', lable: 'NG K' },
];
export default function Program() {
    return (
        <section id="program" className="block program-block">
            <Container fluid>
                <div className="title-holder">
                    <h2>CHNG TRNH O TO CH 2 NM 3 THNG</h2>
                    <div className="subtitle">Hc  lm vic ngay</div>
                </div>
                <Row>
                    {programData.map((program) => {
                        return (
                            <Col sm={3} className="holder">
                                <Card style={{ width: '16rem' }}>
                                    <Card.Img variant="top" src={program.image} />
                                    <Card.Body>
                                        <Card.Title>{program.title}</Card.Title>

                                        <Button variant="primary">{program.lable}</Button>
                                    </Card.Body>
                                </Card>
                            </Col>
                        );
                    })}
                </Row>
            </Container>
        </section>
    );
}
